                <el-form-item label="公司名称" :inline="true">
                    <el-input v-model.trim="filters.company_name" placeholder="请输入公司名称"></el-input>
                </el-form-item>
                <el-form-item label="联系人电话" :inline="true">
                    <el-input v-model.trim="filters.mobile" placeholder="请输入联系人电话"></el-input>
                </el-form-item>
                <el-form-item label="信用代码" :inline="true">
                    <el-input v-model.trim="filters.credit_code" placeholder="请输入信用代码"></el-input>
                </el-form-item>
                <!-- <el-form-item label="担保资质">
                    <el-select v-model="filters.is_assure" placeholder="请选择">
                        <el-option label="全部" :value="1"></el-option>
