                        <el-option label="全部" :value="4"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="信贷状态">
                    <el-select v-model="filters.is_credit" placeholder="请选择">
                        <el-option label="全部" value="1"></el-option>
                        <el-option label="有未结清自贷款" value="2"></el-option>
                        <el-option label="有未结清助贷款" value="3"></el-option>
                        <el-option label="有未结清银行贷款" value="4"></el-option>
                        <el-option label="有未结清担保贷款" value="5"></el-option>
                        <el-option label="无任何未结清贷款" value="6"></el-option>
