                </el-form-item> -->
                <el-button @click="handleSearch">查询</el-button>
                <el-button type="primary" icon="plus" @click="$router.push({ path: '/companyFile/add'})">新建</el-button>
            </el-form>
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="company_name" label="公司名称" width="120">
                </el-table-column>
                <el-table-column prop="contact" label="联系人" width="120">
                </el-table-column>
                <el-table-column prop="mobile" label="移动电话" width="120">
                </el-table-column>
                <el-table-column prop="call_num" label="挂靠车辆">
                </el-table-column>
                <el-table-column prop="assure_num" label="担保车辆">
                </el-table-column>
                <el-table-column prop="is_call" label="挂靠状态">
                </el-table-column>
                <el-table-column prop="is_assure" label="担保资质">
                </el-table-column>
                <el-table-column prop="is_credit" label="信贷状态">
                </el-table-column>
                <el-table-column prop="create_user" label="创建人">
                </el-table-column>
                <el-table-column label="创建日期">
                    <template slot-scope="{row}">
                        {{row.create_time}}
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button type="text" icon="el-icon-view" @click="$router.push({ path: '/companyFile/view', query: {id:row.id, operation_uniqid: row.operation_uniqid}})">查看</el-button>
                        <el-button type="text" icon="el-icon-edit" @click="$router.push({ path: '/companyFile/edit', query: {id:row.id, operation_uniqid: row.operation_uniqid}})">修改</el-button>
                        <el-button type="text" icon="el-icon-delete" @click="handleDelete(row, $index)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :page-size="limit" :total="total">
                </el-pagination>
            </div>
        </div>
    
    </div>
</template>

<script>
    import * as API from '@/api/assetsFile'
    export default {
        name: 'basetable',
        data() {
            return {
                tableData: [],
                page: 1,
                total: 10,
                limit: 10,
                loading: false,
                filters: {
                    company_name: '',
                    mobile: '',
                    credit_code: '',
                    is_assure: '',
                    is_credit: '',
                    is_call: ''
                },
                options: [{
                    value: '1',
                    label: '黄金糕'
                }, {
                    value: '2',
                    label: '双皮奶'
                }, {
                    value: '3',
                    label: '蚵仔煎'
                }, {
                    value: '4',
                    label: '龙须面'
                }, {
                    value: '5',
                    label: '北京烤鸭'
                }],
                idx: -1
            }
        },
        mounted() {
            this.getData();
        },
        computed: {
            
        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                let params = {
                    page: this.page,
                    ...this.filters
                }
                this.loading = true
                API.apiCompanyFileList(params).then(res => {
                    this.loading = false
                    this.tableData = res.data;
                    this.total = res.total;
                })
               
            },
            handleSearch() {
                this.getData();
            },
            handleDelete(row, index) {
                let params = {
                    id: row.id,
                    operation_uniqid: row.operation_uniqid,
                    status: 2
                }
                API.apiCompanyFileEdit(params).then(res => {
                    this.tableData.splice(index, 1);
                })
            }
        }
    }

</script>

